.messageSender{
    display: flex;
    margin-top: 30px;
    flex-direction: column;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
    width: 100%;
}
.messageSender_top{
    display: flex;
    padding: 15px;
    border-bottom: 3px solid #eff2f5;

}
.messageSender_top > form{
    flex: 1;
    display: flex;
}
.messageSender_top > form >input {
    outline-width: 0;
    border: none;
    padding: 5px 20px;
    margin: 0px 10px;
    border-radius: 999px;
    background-color: #eff2f5;

}
.messageSender_top >form >button{
    display: none;
}
.messageSender_input{
    flex: 1;
}
.messageSender_bottom{
    display: flex;
    justify-content: space-evenly;
}

.messageSender_option{
 display: flex;
 align-items: center;
 padding: 20px;
 margin: 5px;
 color: gray;

}
.messageSender_option >h3{
    font-size: medium;
    margin-left: 10px;
    cursor: pointer;
}
.messageSender_option:hover{
    background-color: #eff2f5;
    border-radius: 20px;
}